import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router} from '@angular/router';
import { BlogService } from '../blog.service';
import {Location} from '@angular/common';

@Component({
  selector: 'app-blog-view',
  templateUrl: './blog-view.component.html',
  styleUrls: ['./blog-view.component.css'],
  providers: [Location]
})
export class BlogViewComponent implements OnInit {

  public currentBlog;
 constructor(private _route: ActivatedRoute, private router: Router, public blogService: BlogService, public location: Location) {
   console.log("constructor is called");
  }

  ngOnInit() {

    console.log("ngoninit is called");
    // getting blog id from the route

    let myBlogId = this._route.snapshot.paramMap.get('blogId');

    console.log(myBlogId);
    // this.currentBlog = this.blogService.getSingleBlogInformation(myBlogId);
     this.blogService.getSingleBlogInformation(myBlogId).subscribe(

      data => {
        console.log(data);
        this.currentBlog = data["data"];
      },
      error => {
        console.log("some error occured");
        console.log( error.errorMessage);
      }
    );
  }

  public deleteThisBlog(): any {

    this.blogService.deleteBlog(this.currentBlog.blogId).subscribe(

      data =>{
        console.log("data");
        alert("deleted successfully");
        setTimeout(() => {
          this.router.navigate(['/home']);
        }
        );
      },
      error => {
        console.log("some error occured");
        console.log(error.errorMessage);
      }
    );
  }

  public goBackTopreviousPage(): any {
    this.location.back();
  }

}
