import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { BlogService } from '../blog.service';

@Component({
  selector: 'app-blog-edit',
  templateUrl: './blog-edit.component.html',
  styleUrls: ['./blog-edit.component.css']
})
export class BlogEditComponent implements OnInit {

  public currentBlog;
  public possibleCategories = ["comedy","drama","Action","technology"];
 constructor(private _route: ActivatedRoute, private router: Router, public blogService: BlogService) {
   console.log("constructor is called");
  }

  ngOnInit() {

    console.log("ngoninit is called");
    // getting blog id from the route

    let myBlogId = this._route.snapshot.paramMap.get('blogId');

    console.log(myBlogId);
    // this.currentBlog = this.blogService.getSingleBlogInformation(myBlogId);
     this.blogService.getSingleBlogInformation(myBlogId).subscribe(

      data => {
        console.log(data);
        this.currentBlog = data['data'];
        console.log(this.currentBlog.category);
      },
      error => {
        console.log("some error occured");
        console.log( error.errorMessage);
      }
    );
  }
  public editThisBlog(): any {

    this.blogService.editBlog(this.currentBlog.blogId, this.currentBlog).subscribe(

      data => {
        console.log(data);
        console.log('edited successfully');
        setTimeout(() => {
          this.router.navigate(['/blog', this.currentBlog.blogId]);
        }, 1000 );

      },
      error =>{
        console.log(error.errorMessage);
      }
    )
  }

}
