public createBlog(blogData): any {
  // using for of loop from type script

  let myResponse = this._http.post(this.baseUrl + '/create' + '?authToken=' + this.token, blogData);
  return myResponse;
}

public deleteBlog(blogId): any {
  // using for of loop from type script
let data = {};
  let myResponse = this._http.post(this.baseUrl + '/' + blogId + '/delete' + '?authToken=' + this.token, data);
  return myResponse;
}

public editBlog(blogId,blogData): any {
  // using for of loop from type script

  let myResponse = this._http.put(this.baseUrl  + '/' + blogId + '/edit' + '?authToken=' + this.token, blogData);
  return myResponse;
}


